export default {
	props: {
		// 步骤条的类型，dot|number
		mode: {
			type: String,
			default: 'dot'
		},
		// 步骤条的数据
		list: {
			type: Array,
			default () {
				return []
			}
		},
		// 主题类型, primary|success|info|warning|error
		type: {
			type: String,
			default: 'primary'
		},
		// 当前哪一步是激活的
		current: {
			type: [Number, String],
			default: 0
		},
		// 激活步骤的颜色
		activeColor: {
			type: String,
			default: ''
		},
		// 未激活的颜色
		unActiveColor: {
			type: String,
			default: '#606266'
		}
	},
	data() {
		return {

		}
	},
	computed: {
		innerActiveColor() {
			if (this.activeColor) return this.activeColor;
			else if (this.type) return this.$u.color[this.type];
			else return "#2979ff";
		},
	}
}
